@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bungee+Shade&display=swap');

body,
#container,
#title,
#test,
#Games-container{
    display: flex;
    align-items: center;
    justify-content: center;
}
body{background-color: #62374E;}
#test{flex-direction: column;}

/*All elements - home*/
#container{
    flex-direction: column;
    background-color:rgb(235, 235, 235);
    width: 900px;
    height: 540px;
    border-radius: 5px;
    box-shadow: -14px 14px 19px -1px #3a212e;
    margin: 35px;
    padding-bottom: 30px;
}
#title{
    color: #232229;
    font-size: 60px;
    font-family: 'Bungee Shade', cursive;
    margin: 0px;
}

/*Little games box*/
#choose{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-end;
    background-color:#ffb85b;
    color: white;
    width: 240px;
    height: 270px;
    margin: 0px 10px 10px 10px;
    padding: 10px 10px 20px 10px;
    border: 5px outset #f7af52;
}

h2{font-size: 30px; font-family: 'Fjalla One', sans-serif; margin: 0px; margin-top: 35px;}
p{font-size: 15px; font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;}
#emoji{font-size: 70px;}

#redirect{ /*button*/
    border: none;
    cursor: pointer;
    background-color: #232229;
    color: white;
    border-radius: 5px;
    width: 230px;
    height: 50px;
}

#redirect:hover{
    background-color: #0f0f13;
}
/*Side*/
@media(max-width: 730px){
    #title{
        margin-top: 0px;
        font-size: 35px;
        margin-bottom: 30px;
    }
    #container{
        width: 550px;
        height: 700px;
    }
    #Games-container{
        width: 450px;
        height: 500px;
        flex-direction: column;
    }
    #redirect{
        height: 35px;
        width: 130px;
    }
    #choose{
        width: 450px;
        height: 145px;
    }
    h2{
        margin: 0px;
        margin-bottom: 5px;
    }
    p{
        display: none;
    }
}

/*Phone*/
@media(max-width: 530px){
    #title-container{
        width: 230px;
    }
    #title{
        margin-top: 0px;
        font-size: 35px;
        margin-bottom: 20px;
    }
    #container{
        width: 370px;
        height: 650px;
    }
    #Games-container{
        width: 290px;
        height: 500px;
        flex-direction: column;
    }
    #choose{
        width: 235px;
        height: 140px;
    }
    #redirect{
        margin-bottom: 0px;
    }
}

/*Min*/
@media(max-height: 500px){
    h2{
        font-size: 20px;
    }
    #container{
        width: 550px;
        height: 300px;
        margin: 10px;
    }
    #Games-container{
        width: 500px;
        height: 210px;
        flex-direction: row;
    }
    #choose{
        margin-top: 30px;
        width: 150px;
        height: 150px;
        font-size: 18px;
        flex-direction: column;
    }
    #redirect{
        height: 25px;
        width: 130px;
    }
    h2{
        margin: 10px 10px;
    }
    p{
        display: none;
    }
}